import React, { useState,useContext } from 'react'
import { v4 as uuidv4 } from "uuid";
import { GlobalContext } from '../context/GlobalState'

//for unique id i install uuid package =npm i uuid
const TransactionInputs = () => {
    const {addIncome,addExpense} = useContext(GlobalContext)

    const [income, setIncome] = useState({
       
        incomeText:"",
        incomeAmount:0
    })
    const {incomeText,incomeAmount}=income
    const onChangeIncome=(e)=>{
        setIncome({  ...income,[e.target.name]:e.target.value})
    }
    const onSubmitIncome=(e)=>{
        e.preventDefault()
        if(incomeText!==""){
            const newIncomeTransaction={
                id:uuidv4(),
                incomeText,
                incomeAmount:incomeAmount*1
            }
            addIncome(newIncomeTransaction)
        }
      

    }
    const [expense, setExpense] = useState({
       
        expenseText:"",
        expenseAmount:0
    })
    const {expenseText,expenseAmount}=expense
    const onChangeExpense=(e)=>{
        setExpense({  ...expense,[e.target.name]:e.target.value})
    }
    const onSubmitExpense=(e)=>{
        e.preventDefault()
        if(expenseText!==""){
            const newExpenseTransaction={
                id:uuidv4(),
                expenseText,
                expenseAmount:expenseAmount*1
            }
            addExpense(newExpenseTransaction)
        }
      

    }

    return (
        <div className="form-wrapper">
         <form onSubmit={onSubmitIncome}>
             <div className="input-group income">
             <input type="text" name="incomeText" placeholder="Add Income..." autoComplete="off" onChange={onChangeIncome}></input>
             <input type="number" name="incomeAmount" placeholder="Amount..." autoComplete="off" onChange={onChangeIncome}></input>
             <input type="submit" value="Submit" />
             </div>
         </form>
         <form onSubmit={onSubmitExpense}> 
         <div className="input-group expense">
             <input type="text" name="expenseText" placeholder="Add Expense..." autoComplete="off" onChange={onChangeExpense}></input>
             <input type="number" name="expenseAmount" placeholder="Amount..." autoComplete="off" onChange={onChangeExpense}></input>
             <input type="submit" value="Submit" />
          </div>   
         </form>
            
        </div>
    )
}

export default TransactionInputs
